<template>
    <div>
        <DirectionSelectorController
                v-on:getDestinations="$emit('getDestinations')"
                v-on:search="$emit('search')">
        </DirectionSelectorController>
        <MapController v-if="isMapEnabled"></MapController>
    </div>
</template>

<script>
    import DirectionSelectorController from "@/components/controllers/DirectionSelectorController";
    import MapController from "@/components/controllers/MapController";
    import contextStore from '@/stores/context-store'

    export default {
        name: "MainView",
        components: {
            DirectionSelectorController,
            MapController
        },
        store: [contextStore],
        computed: {
            isMapEnabled: function () {
                return contextStore.getters.getIsMapEnabled;
            }
        },
        methods: {}
    }
</script>