<template>
    <div class="row position-absolute w-100 home-search__container">
        <div class="col-12">
            <h1 class="home-search__title ml-0 pl-0 col-12 col-md-8 col-lg-6 m-lg-0">Want to see something cool along
                the way?
            </h1>
            <div class="home-search__bar d-block">
                <form action="" autocomplete="off"
                      class="d-block d-sm-block d-md-flex d-lg-flex d-xl-flex align-items-center">
                    <div class="home-search__bar__container d-block d-sm-block d-md-flex d-lg-flex d-xl-flex align-items-center">
                        <div class="form-group d-flex">
                            <div class="location-search__container pl-0 pr-0 home-search__bar__extend position-relative home-search__border bl">
                                <div id="checkin" class="location-search" style="flex:1; overflow: hidden">
                                                            <span class="search-input top left first">
                                                                <label for="autosuggest_n_input">From</label>
                                                            </span>
                                    <select name="autosuggest_n_input"
                                            v-model="selectedOrigin"
                                            id="autosuggest_n_input"
                                            class="hero-search-input select-box" style="width: 300px"
                                            @change="validateOrigin">
                                        <option v-for="option in originLocations" :value="option">
                                            {{option.canonic}}
                                        </option>
                                    </select>
                                </div>
                            </div>
                            <div v-if="isOriginValidated"
                                 class="location-search__container pl-0 pr-0 home-search__bar__extend position-relative home-search__border br">
                                <div id="checkout" class="location-search" style="flex:1;">
                                                            <span class="search-input top right middle">
                                                                <label>To</label>
                                                            </span>
                                    <select name="autosuggest__input"
                                            v-model="selectedDestination"
                                            id="autosuggest_input"
                                            class="hero-search-input select-box" style="width: 300px"
                                            @change="selectDestination">
                                        <option v-for="option in destinationLocations" :value="option">
                                            {{option.permalink}}
                                        </option>
                                    </select>
                                    <div class="autosuggest__results-container"></div>
                                </div>
                            </div>
                        </div>
                    </div>


                    <span class="d-flex pt-md-0 pt-lg-0 pt-xl-0 m-auto m-sm-auto m-md-auto ml-lg-auto ml-xl-auto">
                                                <span class="pt-md-0 pt-lg-0 pt-xl-0 m-auto m-sm-auto m-md-auto ml-lg-auto ml-xl-auto">
                                                    <a class="nuxt-link-exact-active nuxt-link-active"
                                                       style="margin:0 auto;">
                                                        <a v-if="isDestinationSelected" class="home-search__button"
                                                           v-on:click="$emit('search')">Search</a>
                                                    </a>
                                                </span>
                                            </span>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import contextStore from '@/stores/context-store'

    export default {
        name: "DirectionSelectorView",
        props: ['originLocations', 'destinationLocations'],
        store: [contextStore],
        data: function () {
            return {
                selectedOrigin: {},
                selectedDestination: {},
                isOriginValidated: false,
                isDestinationSelected: false
            }
        },
        methods: {
            validateOrigin: function () {
                //This is a validation to return the results from the mock request
                // Only works for Lisbon
                if (this.selectedOrigin.canonic === 'Lisbon') {
                    this.setLocationToStore('origin', this.selectedOrigin);
                    this.isOriginValidated = true;
                    return this.$emit('isOriginDefined', this.isOriginValidated);
                }
                this.setLocationToStore('origin', {});
                this.isOriginValidated = false;
                return this.$emit('isOriginDefined', this.isOriginValidated);

            },
            setLocationToStore: function (type, location) {
                contextStore.commit('setLocation', {type: type, value: location});
            },
            selectDestination: function () {
                this.setLocationToStore('destination', this.selectedDestination);
                this.isDestinationSelected = true;
            }
        }
    }
</script>