<template>
    <DirectionSelectorView
            v-bind:originLocations="getOriginLocations"
            v-on:isOriginDefined="defineOrigin"
            v-bind:destinationLocations="getDestinationLocations"
            v-on:search="$emit('search')">
    </DirectionSelectorView>
</template>

<script>
    import DirectionSelectorView from "@/components/views/DirectionSelectorView.vue";
    import locationStore from "@/stores/location-store"
    import contextStore from "@/stores/context-store"
    import {requestHandler} from "../../mixins/requestHandler";

    export default {
        name: "DirectionSelectorController",
        components: {
            DirectionSelectorView
        },
        mixins: [requestHandler],
        store: [locationStore, contextStore],
        data: function () {
            return {
                originLocations: [],
                isOriginDefined: false,
                destinationLocation: {}
            }
        },
        computed: {
            getOriginLocations: function () {
                return locationStore.getters.getOriginLocations;
            },
            getDestinationLocations: function () {
                return locationStore.getters.getDestinationLocations;
            }
        },
        methods: {
            defineOrigin: function (event) {
                this.isOriginSelected = event;
                if (this.isOriginSelected) {
                    this.$emit('getDestinations');
                }
            }
        }

    }
</script>