<template>
    <div>
        <NavBarView></NavBarView>
        <MainView v-on:getDestinations="getPossibleDestinations" v-on:search="search"></MainView>
    </div>
</template>

<script>
    import MainView from "@/components/views/MainView.vue"
    import NavBarView from "@/components/views/NavBarView.vue"
    import {requestHandler} from "../../mixins/requestHandler";
    import {utils} from "../../mixins/utils";
    import locationStore from "@/stores/location-store"
    import contextStore from "@/stores/context-store"

    export default {
        name: "MainController",
        components: {
            MainView,
            NavBarView
        },
        store: [locationStore, contextStore],
        mixins: [requestHandler, utils],
        data: function () {
            return {
                apiUrl: {
                    locationDataUrl: 'http://www.mocky.io/v2/5d3b4acf3000006162a2a06b',
                    // Because of CORS, I can't do a query to your service, I mocked the results from Lisbon with 10 results
                    possibleDestinationsUrl: 'http://www.mocky.io/v2/5d3b4efc300000e85ea2a07b'
                },
                locations: [],
                mockHighlights: [{
                    name: "Emotion - Life on adventure",
                    description: "Outdoor activity company based in a country house hotel offering a range of activities for all ages and interests. " +
                        "With us you can discover the nature, culture, gastronomy, history and traditions of the Alentejo. " +
                        "You can do this on foot, bike, kayak, 4wd, or or hot air balloon. It's your choice.",
                    location: "37.8892767,-8.0205536",
                    media: 'https://d3rr2gvhjw0wwy.cloudfront.net/uploads/activity_teasers/52009/600x400-1-50-b02ed1991165ece68b88e9fb602fa2e1.jpg',
                    selected: false
                }, {
                    name: "Sines Castle",
                    description: "The surviving remains of the original mediaeval structure of Sines Castle include the walls crowned by battlements and the keep. " +
                        "The walls include Visigoth architectural elements that formed part of a church built before the seventh century. " +
                        "The interior of the castle includes two painted wooden ceilings.",
                    location: "37.9553, -8.8662",
                    media: 'https://upload.wikimedia.org/wikipedia/commons/thumb/8/89/Sines03.jpg/235px-Sines03.jpg',
                    selected: false
                }, {
                    name: "Santa Clara Dam",
                    description: "The reservoir is surrounded by round hills covered by gum rockroses and tree-filled margins. " +
                        "The silence, the landscape and the peaceful waters make this place a truly dreamlike spot. " +
                        "The typical Santa Clara-a-Velha and Sabóia villages are also good visiting tips.",
                    location: "37.513528, -8.441701",
                    media: 'http://en.lifecooler.com/lifecooleren/content/168969.jpg',
                    selected: false
                }]
            }
        },
        created: function () {
            this.getLocationData();
        },
        methods: {
            /**
             * Fetches the possible origin from the API
             * Returns an array
             * Saves the possible origin locations to the location store
             */
            getLocationData: async function () {
                this.locations = await this.fetchDataFromUrl(this.apiUrl.locationDataUrl);
                this.saveDataToStore('origin', this.locations);
            },
            /**
             * Fetches data from the API and returns an array with 10 possible destinations
             * @returns an array with all the possible destinations, excluding the one that is the same as the origin
             * Saves the possible destinations to the context store
             */

            getPossibleDestinations: async function () {
                let me = this;
                this.possibleDestinations = await this.fetchDataFromUrl(this.apiUrl.possibleDestinationsUrl);
                // locations returned from the call with lowercase, this method converts the locations to Pascal Case
                this.possibleDestinations.forEach(function (location) {
                    location.coordinates = me.getCoordinatesFromOriginLocations(location.permalink);
                    location.permalink = me.convertToPascalCase(location.permalink);
                });
                // locations equal to the origin may return, with this method we splice them from the array
                this.possibleDestinations.splice(this.possibleDestinations.findIndex(function (location) {
                    return location.permalink === contextStore.getters.getLocation('origin').canonic;
                }), 1);
                // save the possible destinations to the locationStore store
                this.saveDataToStore('destination', this.possibleDestinations);
            },
            saveDataToStore: function (typeOfData, locations) {
                switch (typeOfData) {
                    case 'origin':
                        locationStore.commit('setLocations', {type: 'origin', value: locations});
                        break;
                    case 'destination':
                        locationStore.commit('setLocations', {type: 'destination', value: locations});
                        break;
                    case 'highlights':
                        contextStore.commit('setHighlights', locations);
                        break;
                }
            },
            getCoordinatesFromOriginLocations: function (permalink) {
                return this.locations.find(function (elem) {
                    if (elem.permalink === permalink) {
                        return {
                            latitude: elem.latitude,
                            longitude: elem.longitude
                        };
                    }
                })
            },
            getHighlightsFromServer: function () {
                this.highlights = this.mockHighlights;
                this.saveDataToStore('highlights', this.highlights);
                window.console.log('highlights: ' + this.highlights);
                contextStore.commit('setIsMapEnabled', true);
            },
            search: function () {
                this.getHighlightsFromServer();
            }
        }
    }
</script>
