<template>
    <div class="flex-container flex-6-container margin-top-375">
        <MapView :latitude="latitude"
                 :longitude="longitude"
                 :origin="map.origin"
                 :destination="map.destination"
                 :key="componentKey"
                 class="flex-3-start margin-right-10"></MapView>
        <HighlightsList v-if="isHighlightListEnabled"
                        v-on:selectHighlights="selectHighlights"
                        class="flex-3-end margin-left-10"></HighlightsList>
    </div>
</template>

<script>
    import MapView from '@/components/views/MapView.vue'
    import HighlightsList from '@/components/HighlightsList.vue'
    import contextStore from '@/stores/context-store'
    import {utils} from "../../mixins/utils";

    export default {
        name: "MapController",
        components: {
            MapView,
            HighlightsList
        },
        mixins: [utils],
        store: [contextStore],
        data: function () {
            return {
                isHighlightListEnabled: true,
                originLocation: {},
                destinationLocation: {},
                latitude: '',
                longitude: '',
                map: {
                    origin: '',
                    destination: '',
                    travelMode: 'DRIVING'
                },
                selectedHighlights: [],
                componentKey: 0
            }
        },
        created: function () {
            this.getMapCoordinates();
        },
        methods: {
            forceMapReRender: function () {
                this.componentKey += 1;
            },
            getMapCoordinates: function () {
                this.originLocation = contextStore.getters.getLocation('origin');
                this.destinationLocation = contextStore.getters.getLocation('destination');
                let originCoordinates = {
                        latitude: this.originLocation.latitude,
                        longitude: this.originLocation.longitude
                    },
                    destinationCoordinates = {
                        latitude: this.destinationLocation.coordinates.latitude,
                        longitude: this.destinationLocation.coordinates.longitude
                    };
                this.map.origin = originCoordinates.latitude.toString() + ', ' + originCoordinates.longitude.toString();
                this.map.destination = destinationCoordinates.latitude.toString() + ', ' + destinationCoordinates.longitude.toString();

                this.latitude = this.getAverage(originCoordinates.latitude, destinationCoordinates.latitude);
                this.longitude = this.getAverage(originCoordinates.longitude, destinationCoordinates.longitude);
            },
            selectHighlights: function (selectedHighlights) {
                this.selectedHighlights = selectedHighlights;
                contextStore.commit('selectHighlights', selectedHighlights);
                this.forceMapReRender();
            }
        }
    }
</script>