import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        locations: {
            origin: {},
            destination: {}
        }
    },
    mutations: {
        setLocations: function (state, payload) {
            state.locations[payload.type] = payload.value;
        },
        setPossibleDestinations: function (state, value) {
            state.possibleDestinations = value;
        }
    },
    getters: {
        getOriginLocations: function (state) {
            return state.locations.origin;
        },
        getDestinationLocations: function(state) {
            return state.locations.destination;
        },
        getLocationByCity: function (state) {
            return function (city) {
                return state.locations.origin.find(function (elem) {
                    return elem.canonic.toLowerCase() === city.toLowerCase();
                })
            }
        },
        getPossibleDestinations: function (state) {
            return state.possibleDestinations;
        }
    }
});