<template>
    <div class="highlights-area">
        <h1 class="text-center padding-top-bottom-20">Highlights along the way:</h1>
        <div v-for="highlight in highlights" v-bind:key="highlight.name">
            <div class="text-center">
                <h2 class="text-black-50">{{highlight.name}}</h2>
                <p>{{highlight.description}}</p>
            </div>
            <br>
            <div class="flex-container flex-2-container">
                <div class="flex-2-start">
                    <img :src="highlight.media" alt="" class="highlight-image">
                </div>
                <div class="flex-2-end card-button">
                    <button v-if="!highlight.selected" class="home-search__button" @click="selectHighlight(highlight)">
                        Add
                    </button>
                    <button v-else class="home-search__button remove-highlight-button"
                            @click="selectHighlight(highlight)">
                        Remove
                    </button>
                </div>
            </div>
            <hr>
        </div>
    </div>
</template>

<script>
    import contextStore from "@/stores/context-store"

    export default {
        name: "HighlightsList",
        store: [contextStore],
        data: function () {
            return {
                checked: false,
                highlights: [],
                selectedHighlights: []
            }
        },
        created: function () {
            this.getHighlights();
        },
        methods: {
            getHighlights: function () {
                this.highlights = contextStore.getters.getHighlights;
            },
            selectHighlight: function (value) {
                value.selected = !value.selected;
                this.updateSelectedHighlights();
            },
            updateSelectedHighlights: function () {
                this.selectedHighlights = this.highlights.filter(function (highlights) {
                    return highlights.selected;
                });
                this.$emit('selectHighlights', this.selectedHighlights);
            }
        }
    }
</script>