<template>
    <div id="map">
    </div>
</template>

<script>
    import gmapsInit from '@/mixins/gMaps';
    import contextStore from '@/stores/context-store'

    export default {
        name: "MapView",
        props: ['latitude',
            'longitude',
            'origin',
            'destination'],
        store: [contextStore],
        data: function () {
            return {
                selectedHighlights: [],
                google: null
            }
        },
        updated: function () {
            this.reRenderMap(this.getWaypoints);
        },
        created: function () {
            this.getSelectedHighlights();
        },
        async mounted() {
            try {
                this.google = await gmapsInit();
                this.initMap();
            } catch (error) {
                window.console.error(error);
            }
        },
        methods: {
            getSelectedHighlights: function () {
                this.selectedHighlights = contextStore.getters.getSelectedHighlights;
            },
            getWaypoints: function () {
                let waypoints = [];
                for (let i = 0; i < this.selectedHighlights.length; i++) {
                    let tempElem = {};
                    tempElem.location = this.selectedHighlights[i].location;
                    tempElem.stopover = true;
                    waypoints.push(tempElem);
                }
                return waypoints;
            },
            initMap: function () {
                let directionsService = new this.google.maps.DirectionsService,
                    directionsDisplay = new this.google.maps.DirectionsRenderer,
                    map = new this.google.maps.Map(document.getElementById('map'), {
                        zoom: 7,
                        center: {lat: this.latitude, lng: this.longitude}
                    });
                this.calculateAndDisplayRoute(directionsService, directionsDisplay);
                directionsDisplay.setMap(map);
            },
            calculateAndDisplayRoute: function (directionsService, directionsDisplay) {
                let waypoints = this.getWaypoints();
                if (typeof waypoints[0] == "function") {
                    directionsService.route({
                        origin: this.origin,
                        destination: this.destination,
                        travelMode: 'DRIVING',
                        provideRouteAlternatives: true
                    }, function (response, status) {
                        if (status === 'OK') {
                            directionsDisplay.setDirections(response);
                        } else {
                            window.alert('Directions request failed due to ' + status);
                        }
                    });
                } else {
                    directionsService.route({
                        origin: this.origin,
                        destination: this.destination,
                        travelMode: 'DRIVING',
                        provideRouteAlternatives: true,
                        waypoints: waypoints
                    }, function (response, status) {
                        if (status === 'OK') {
                            directionsDisplay.setDirections(response);
                        } else {
                            window.alert('Directions request failed due to ' + status);
                        }
                    });
                }

            }
        }
    }
</script>