import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        locations: {
            origin: {},
            destination: {}
        },
        isMapEnabled: false,
        highlights: [],
        selectedHighlights: []
    },
    mutations: {
        setLocation: function (state, payload) {
            state.locations[payload.type] = payload.value;
        },
        setIsMapEnabled: function (state, boolean) {
            state.isMapEnabled = boolean
        },
        setHighlights: function(state, highlights) {
            state.highlights = highlights;
        },
        selectHighlights: function (state, locations) {
            state.selectedHighlights = locations;
        }
    },
    getters: {
        getLocation: function (state) {
            return function (type) {
                return state.locations[type];
            }
        },
        getIsMapEnabled: function (state) {
            return state.isMapEnabled;
        },
        getHighlights: function(state) {
            return state.highlights;
        },
        getSelectedHighlights: function (state) {
            return state.selectedHighlights;
        }
    }
});